import SmoothScroll from 'smooth-scroll';

var m768 = matchMedia('screen and (min-width: 768px)');

if (m768.matches) {
    new SmoothScroll('a[href*="#"]', {
        speed: 300,
        offset: 160
    });
} else {
    new SmoothScroll('a[href*="#"]', {
        speed: 300,
        offset: 97
    });
}
