import Swiper from 'swiper';
import { carousel } from '../elements';
import cdnLoad from './cdnLoad';

if (carousel.container !== null) {
    cdnLoad();

    new Swiper('.swiper-container', {
        slidesPerView: 1,
        // spaceBetween: 10,
        loop: true,
        pagination: {
            el: '.swiper-pagination',
            clickable: true
        },
        navigation: {
            nextEl: '.carousel-button-next',
            prevEl: '.carousel-button-prev'
        },
        breakpoints: {
            768: {
                slidesPerView: 2,
                slidesPerGroup: 2,
                loopFillGroupWithBlank: true
            },
            1024: {
                slidesPerView: 3,
                slidesPerGroup: 3,
                loopFillGroupWithBlank: true
            }
        }
    });
}
